import React, { useEffect, useState } from "react";
import CardProducto from "../components/CardProducto";
import Producto from "../interfaces/Producto";
import Footer from "../partials/Footer";
import Navbar from "../partials/Navbar";
import * as ProductoServices from "../services/ProductosServices";
import ScrollReveal from "scrollreveal";
const Home = () => {
  const [productos, setProductos] = useState<Producto[]>([]);
  useEffect(() => {
    getProductos();
    const config = {
      duration: 500,
      delay: 50,
      easing: "ease",
    };
    ScrollReveal().reveal(".show", config);
    return () => {
      setProductos([]);
    };
  }, []);
  const getProductos = async () => {
    const res = await ProductoServices.getAll(1, "");
    setProductos(res.data);
  };
  return (
    <>
      <Navbar />
      <div className="container show" style={{ padding: "5rem 3rem" }}>
        <div className="row justify-content-between">
          {productos.map((producto) => {
            return <CardProducto key={producto.id_producto} producto={producto} />;
          })}
        </div>
      </div>
      <Footer />
    </>
  );
};

export default Home;
